import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BsbuttonsComponent } from './bsbuttons/bsbuttons.component';
import { CboxComponent } from './cbox/cbox.component';
import { IsettingsComponent } from './isettings/isettings.component';
import { RbuttComponent } from './rbutt/rbutt.component';

const routes: Routes = [{path:'bsbuttons', component: BsbuttonsComponent},
{path:'cbox', component: CboxComponent},
{path:'rbutt', component: RbuttComponent},
{path: 'isettings', component: IsettingsComponent}];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
